.App {
  text-align: center;
  overflow-x: hidden;
}
.oh {
  overflow-x: hidden;
}
sup, sub {
  font-size:60%
}

.ffBarlow {
  font-family: 'Barlow', sans-serif;
}

/* Navbar */
.navbar-light .navbar-nav .nav-link {
  color:#003d6a;
  font-weight:700;
}
.dropdown-item {
  color:#003d6a;
  border-bottom:2px solid #e7472c;
  padding-left:0;

}
.dropdown-item.active, .dropdown-item:active {
  background-color: #f1f1f1 !important;
  color:#003d6a;
}
.sideMenuItem {
  color:#003d6a;
  font-size:20px;
  padding: 5px;
  
}
.sideMenuItem:hover {
  color:#003d6a;
  
}
.navLink {
  color:#003d6a;
  font-weight:700;
}
.navLink:hover {
  color:#e7472c;
  
}
.active {
  color:#e7472c;
  font-weight:700;
}
.active2 {
  
  background-color:#f1f1f1;
}
.borNav {
  border-top:3px solid #003d6a;
}
.refesLinks {
  color:#706f6f
}
.refesLinks:hover {
  color:#706f6f
}

/* Links */
a:hover{
  text-decoration: none;
  color:#ffffff
}
.mailLink:hover{
  color:#003d6a;
}
Link:hover {
text-decoration: none;
}

/* SideMenu */
.sideMenu {
  border-bottom:1px solid #e7472c;
}
.sideActive {
  border-left:4px solid #e7472c;
}
.menuButton {
  background-color:#003d6a;
  font-weight:700;
  color:#ffffff;
  padding:0.75rem 1rem;
  border-radius:15px;

}
.mob-remove {
  display:block;
  
}


/* colors */
.white {
  color:#ffffff;
}
.black {
  color:#000000;
}
.blue {
  color: #003d6a;
}
.blueL {
  color: #7fcacd;
}
.orange {
  color:#e7472c;
}
.grey {
  color:#706f6f
}
.bgBlue {
  background-color: #003d6a;
}
.bgLBlue {
  background-color: #7fcacd;
}
.bgBlueL {
  background-color: #eef8f8;
}
.bgWhite {
  background-color: #ffffff;
}
.bgGrey {
  background-color: #f1f1f1;
}

/* Modals */
.ReactModal__Content {
  border:1px solid #7fcacd !important;
  position: absolute;
  background-color: #fff;
  box-shadow: 0 0 10px 0 rgba(0,0,97,0.5);
  overflow:scroll!important;
  font-family: 'Barlow', sans-serif;  
}
.modal-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}


/* Fonts */
.fs11 {font-size:11px;line-height: 14px;}
.fs12 {font-size:12px;line-height: 15px;}
.fs13 {font-size:13px;line-height: 16px;}
.fs14 {font-size:14px;line-height: 17px;}
.fs15 {font-size:15px;line-height: 18px;}
.fs16 {font-size:16px;line-height: 19px;}
.fs19 {font-size:19px;line-height: 22px;}
.fs20 {font-size:20px;line-height: 23px;}
.fs24 {font-size:24px;line-height: 28px;}
.fs30 {font-size:30px;line-height: 33px;}
.wordBreak {
 word-break: break-all;
}

/* Inicio */
.texto-vertical-2 {
  writing-mode: vertical-lr;
  transform: rotate(180deg);
}
.bgInicio {
  background-image: url(images/inicio-background.jpg);
  background-repeat: no-repeat;
  background-size:cover;
  background-position:top left;
}
.bgTitle {
  background-image: url(images/bg-tittle.png);
  background-repeat: no-repeat;
  background-size:cover;
  background-position:top left;
}
.titleText {
  font-size:30px;
  line-height: 33px;
  color:#ffffff;
  text-align:left;
}

.borR {
  border-radius:15px;
}

.bullet li {
  list-style: none;
}

/* Set the list image as background image*/
.bullet li::before {
  content: '';
  display: inline-block;
  margin-right: 15px;

  /* Height of the list image*/
  height: 15px;

  /* Width of the list image*/
  width: 15px;
  background-image: url('images/bullet.png');
  background-size: contain;
}

.ftButton {
  background-color: #ffffff;
  border:1px solid #7fcacd;
  border-radius: 36px;
  padding:3px 10px;
  color:#706f6f
  
}
.ftButton:hover{
  color:#706f6f;
  
}
.button0 {
  background:none;
  border:none
}
.logoFT {
  width:30px
}

/* Papers */
.PaperButton {
  background-color: #7fcacd;
  color:#ffffff;
  border-radius:5px;
  padding:3px 15px;
  
}
.PaperButton:hover {
  background-color: #003d6a;

  
}
.paperCard {
  border-radius:15px;
  -webkit-box-shadow: 1px 3px 11px 1px rgba(112,111,111,0.69);
  -moz-box-shadow: 1px 3px 11px 1px rgba(112,111,111,0.69);
  box-shadow: 1px 3px 11px 1px rgba(112,111,111,0.69);

}
.bgTiCard {
  background-color: #003d6a;
  color:#ffffff;
  padding:3px 10px;
  border-radius:36px;
  font-weight:700;
}

/* Manejo */
.manejoCard {
  background-color: #eef8f8;
  -webkit-box-shadow: inset 0px 0px 6px 0px rgba(112,111,111,1);
-moz-box-shadow: inset 0px 0px 6px 0px rgba(112,111,111,1);
box-shadow: inset 0px 0px 6px 0px rgba(112,111,111,1);

}
.w280 {
  width:280px;
  & a:hover {
    color:#003d6a
  }
}
.mw900 {
  max-width:898px;
}
.borA {
  border:2px solid #7fcacd;
}

.reaccionesTabla {
  border-spacing: 8px;
  border-collapse: separate;
  width:100%;
}
.cell1 {
  background-color:#09a7ac;
  border:1px solid #09a7ac;
  font-weight: 700;
  border-radius:6px;
  color:#ffffff;
  vertical-align:middle;
  padding:5px;
  text-align:center;
}
.cell2 {

  border:2px solid #f1f1f1;
  border-radius:6px;
  color:#706f6f;
  vertical-align:middle;
  padding:5px 15px;
  text-align: left;

}
.cell3 {
  background-color:#f1f1f1;
  border:1px solid #f1f1f1;
  border-radius:6px;
  color:#706f6f;
  vertical-align:middle;
  padding:5px 15px;
  text-align: left;

}
.interBullet {
  color:#e7472c;
  list-style-type: square;
}
.paddT {
  padding:50px;
}


/* Curso */
.img-curso {
  width:283px
}
.bgWhiteO {
  background-color: rgba(265, 265, 265, 0) !important;
}
.bgOpacity {
  background-color: rgba(265, 265, 265, 0)
}

/* Media Queries */

@media (max-width: 1024px) {
  .mob-remove{
    display:none;
  };
}

@media only screen and (max-width: 900px) {
  .bgInicio {
    background-image: url(images/inicio-background-opc.jpg);
  }
} 


@media only screen and (max-width: 800px) {
  .paperCard {
    flex-direction: column;
    align-items:flex-start;
   
   }
   .center {
    align-items: center !important;
    justify-content: center !important;
  }
  .img-curso {
    width:170px
  }

 
}

@media only screen and (max-width: 500px) {
  .paddT {
   padding:20px;

  }
  .padd0 {
    padding:10px!important;
  }
  .reaccionesTabla {
    border-spacing: 4px;
  
  }
  .titleText {
    font-size:25px;
    line-height: 28px;
  }
}